import React from 'react';
import s from './Navbar.module.css';
import { NavLink } from "react-router-dom";
import { SidebarDataType } from "types/commonTypes";

const Navbar: React.FC<Props> = ({sidebarData}
) => {
    return (
        <>
            <nav className={s.nav}>
                <div className={s.item}>
                    <NavLink to='/profile' activeClassName={s.activeLink}>Profile</NavLink>
                </div>
                <div className={s.item}>
                    <NavLink to='/dialogues' activeClassName={s.activeLink}>Messages</NavLink>
                </div>
                <div className={s.item}>
                    <NavLink to='/users' activeClassName={s.activeLink}>Users</NavLink>
                </div>
                <div className={s.item}>
                    <NavLink to='/news' activeClassName={s.activeLink}>New</NavLink>
                </div>

                <div className={s.friendsContainer}>
                    <h2>Friends</h2>
                    {sidebarData.map((f, i) =>
                        <div key={i} className={s.friend}>
                            <img
                                src="data:image/jpeg;base64,"
                                alt=""/>
                            {f.name}
                        </div>
                    )}
                </div>
            </nav>
        </>

    );
};

type Props = {
    sidebarData: SidebarDataType[]
}

export default Navbar;
